*{box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:radial-gradient(circle, #0f181d, #0c1316, #050808);transition:0.4s ease-in-out}html,body,#root{width:100%;height:100%;margin:0;padding:0;overflow:hidden}.app{width:100%;height:100%;overflow-x:hidden}.anim{width:100vw;height:100vh;position:absolute;z-index:1}.landingPage{font-family:'Abel', sans-serif;color:#fff;text-shadow:0px 3px 4px #00000062;text-align:left;font-size:40px;z-index:5;width:100%;height:100%;display:table;position:relative;width:95%}.landingPage p{vertical-align:middle;display:table-cell;text-align:center}@media (max-width: 90rem){.landingPage{font-size:35px}}@media (max-width: 60rem){.landingPage{font-size:25px}}b{text-shadow:0px 0px 10px rgba(255,255,255,0.6)}.particles{position:absolute;top:0;left:0;bottom:0;right:0;z-index:-99999}::-webkit-scrollbar{width:4px}::-webkit-scrollbar-track{box-shadow:inset 0 0 5px rgba(73,73,73,0.3);border-radius:10px}::-webkit-scrollbar-thumb{background:#fff;border-radius:10px}

header{position:absolute;z-index:99;width:100%;padding:30px;background-color:rgba(0,0,0,0.247);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);font-size:20px}header .header-inner{display:flex;align-items:center;justify-content:space-between}header .header-inner nav ul{margin:0;padding:0;display:flex}header .header-inner nav ul li{list-style:none}header .header-inner nav ul li p{font-family:'Abel', sans-serif;text-transform:capitalize;margin:0 40px;color:#ececec}header .header-inner nav ul li p:hover{text-shadow:0px 0px 20px #fff}

.timeline{padding-top:0rem;font-family:'Abel', sans-serif;font-size:20px;color:white;padding-bottom:6rem}.vertical-timeline-element-content{background-color:rgba(0,0,0,0.29);-webkit-backdrop-filter:blur(1.5px);backdrop-filter:blur(1.5px);border:2px solid rgba(255,255,255,0.8);text-shadow:0px 0px 0px rgba(255,255,255,0)}.vertical-timeline-element-content-arrow{border-right:12px solid rgba(255,255,255,0.8)}.vertical-timeline::before{background:rgba(255,255,255,0.1)}.vertical-timeline-element-icon{box-shadow:0 0 0 2px #fff,inset 0 2px 0 rgba(0,0,0,0.08),0 3px 0 4px rgba(0,0,0,0.05)}@media (max-width: 90rem){.timeline{padding-top:0rem}}@media (max-width: 73rem){.vertical-timeline::before{background:rgba(255,255,255,0)}}

.about-grid-container{width:80%;background-color:rgba(0,0,0,0.39);box-shadow:0 8px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.15);border-color:rgba(0,0,0,0.36);color:white;border:1px solid rgba(255,255,255,0.8);border-color:white;border-radius:5px;display:grid;grid-template-columns:auto auto;grid-column-gap:3rem;-webkit-column-gap:3rem;column-gap:3rem;grid-template-areas:'profilephoto aboutmetitle' 'profilephoto aboutmetext' 'profilephoto cvbutton';justify-items:stretch;margin:auto;text-align:justify;padding:2.5rem;border-style:solid;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);margin-bottom:10em}.about-grid-item{text-align:left;width:100%;margin:auto auto;color:#FFF;font-family:'Abel', sans-serif;font-size:18px}.about-profile-photo{grid-area:profilephoto}.about-profile-photo img{border-radius:200%;box-shadow:0 4px 18px 0 rgba(0,0,0,0.2),0 16px 40px 0 rgba(0,0,0,0.9);background-size:cover;background-position:center;max-width:350px;width:35vh;height:35vh}.about-title{grid-area:aboutmetitle;font-family:'Abel', sans-serif;font-size:50px;text-align:left}.about-description{grid-area:aboutmetext}.about-cv-button{grid-area:cvbutton;padding-top:1em;text-align:left}.about-btn{display:inline-block;color:#FFFF;font-family:'Abel', sans-serif;font-size:1.1rem;text-decoration:none;border-width:2px;border-style:solid;border-radius:10px;padding:.5em 0.9em;width:175px;text-align:center}.about-btn:hover{background:rgba(255,255,255,0.1);color:#FFFFFF}@media (max-width: 85rem){.about{height:auto}.about-grid-container{grid-row-gap:0rem;row-gap:0rem;grid-column-gap:0rem;-webkit-column-gap:0rem;column-gap:0rem;width:83%;grid-template-areas:'aboutmetitle' 'aboutmetext' 'cvbutton'}.about-grid-item{text-align:left}.about-profile-photo img{width:0vh;height:0vh}.about-btn{font-size:0.9rem;width:150px}}@media (max-width: 50rem){.about-title{font-size:1.6rem}.about-description{font-size:1rem}}

.social-container{background:rgba(0,0,0,0.5);color:white;padding:25px 50px;margin-top:5rem;text-align:center;-webkit-backdrop-filter:blur(1.3px);backdrop-filter:blur(1.3px);font-size:17px;font-family:'Abel', sans-serif}a.social{margin:0 2rem;transition:transform 250ms;display:inline-block}a.social:hover{transform:translateY(-4px)}a.linkedin{color:#ffffff}a.linkedin:hover{box-shadow:0px 0px 20px rgba(255,255,255,0.4);border-radius:10%}a.github{color:#ffffff}a.github:hover{box-shadow:0px 0px 20px rgba(255,255,255,0.4);border-radius:100%}.social-container hr{margin-top:1.5rem;width:40%;border:1px solid rgba(255,255,255,0.35)}.social-container p{color:rgba(255,255,255,0.35)}

.skills-container{width:100%}.skills-bg{width:600px;height:600px;background-color:rgba(0,0,0,0.3);border-radius:100%;-webkit-backdrop-filter:blur(1.3px);backdrop-filter:blur(1.3px);border:3px solid rgba(255,255,255,0.8);margin:0 auto}.skills-container img{background-size:cover;background-position:center;width:100%;height:100%}@media (max-width: 90rem){.skills-bg{width:500px;height:500px}}@media (max-width: 60rem){.skills-bg{width:400px;height:400px}}@media (max-width: 40rem){.skills-bg{width:300px;height:300px}}

.gameDetailsRightContainer,.gameDetailsLeftContainer{width:100%;height:100%}.gameDetailsLeftContainer{background:linear-gradient(-90deg, #000 0%, #000 0%, rgba(0,0,0,0) 58%, rgba(0,0,0,0) 100%)}.gameDetailsRightContainer{background:linear-gradient(90deg, #000 0%, #000 0%, rgba(0,0,0,0) 58%, rgba(0,0,0,0) 100%)}.gameDetailsLeft,.gameDetailsRight{font-size:5rem;color:#fff;width:50%;max-width:50%;height:100%;padding-left:2rem;padding-right:2rem;flex-direction:row;display:flex;align-items:center;justify-content:center}.gameDetailsLeft{float:right}.gameDetailsLeft img,.gameDetailsLeft .gitHub-btn{float:left}.gameDetailsLeft p{float:left;padding-right:6em}.gameDetailsRight{float:left}.gameDetailsRight img,.gameDetailsRight .gitHub-btn{float:right}.gameDetailsRight p{float:right;padding-left:6em}.gameDetailsRight p,.gameDetailsLeft p{font-family:'Abel', sans-serif;color:rgba(252,242,208,0.863);font-size:1.9rem;text-align:justify;text-shadow:5px 5px 5px #000000;padding-bottom:1em}.gameDetailsRight img,.gameDetailsLeft img{width:100%;max-width:80%}.gameDetailsRight .gitHub-btn,.gameDetailsLeft .gitHub-btn{font-family:'Abel', sans-serif;display:inline-block;font-size:1.9rem;color:rgba(252,242,208,0.863);background:rgba(252,242,208,0.05);width:40%;height:25%;text-align:center;text-decoration:none;border-width:2px;border-style:solid;border-radius:10px;padding:.25em}.gameDetailsRight .gitHub-btn:hover,.gameDetailsLeft .gitHub-btn:hover{background:rgba(252,242,208,0.1)}.scrollArea{position:absolute;top:0;left:0;width:100vw;height:100vh;overflow-y:scroll;overflow-x:hidden}.loading{position:absolute;top:0px;left:0px;width:100%;height:100%;background:#000000;display:flex;align-items:center;justify-content:center;z-index:1000}.loading-bar-container{width:100px;height:20px;border-radius:5px;background:#272727}.loading-bar{height:20px;border-radius:5px;background:#ffffff}@media (max-width: 90rem){.gitHub-btn{font-size:1.6rem}}@media (max-width: 60rem){.gitHub-btn{font-size:1.2rem}}@media (max-width: 40rem){.skills-bg{width:300px;height:300px}}@media (max-width: 1440px){.gameDetailsRight p,.gameDetailsLeft p{font-size:1.3rem}}@media (max-width: 720px){.gameDetailsRight p,.gameDetailsLeft p{font-size:1rem}}

